.message {
    position: relative;
    margin-bottom: 40px;
}

/*

width: 15px;
height: 15px;
background: radial-gradient(triangle at 0 0,
   rgba(0, 0, 0, 0) 15px,
   #0066CC 0);
*/

.imageAndText {
    position: relative;
    display: flex;
    flex-direction: row-reverse;
    align-items: flex-end;
}

.imageAndText img {
    width: 48px;
    height: 48px;
    margin: 0 70px 0 15px;
}

.text {
    color: #FFFFFF;
    font-size: 16px;
    font-family: Montserrat;
    text-align: right;
    background-color: #0066CC;
    border-radius: 5px;

}

.ugol {
    width: 15px;
    height: 15px;
    background: radial-gradient(circle at 0 0, rgba(0, 0, 0, 0) 13px, #0066CC 0);
    transform: rotateY(180deg);
    position: absolute;
    right: 121px;
    top: 43px;
}

.text:after {

}

.name {
    font-size: 18px;
    font-family: Montserrat;
    color: #FFFFFF;
    padding-top: 8px;
    padding-right: 14px;
    font-weight: bold;
}

.messageText {
    font-size: 16px;
    font-family: Montserrat;
    text-align: right;
    margin-right: 15px;
    padding: 3px 0 8px 13px;
}

.time {
    position: absolute;
    right: 14px;
    color: #000000;
    font-size: 10px;
    font-family: Montserrat;
    margin: 5px 70px 0 0;

}